.toolbar {
    top: 0;
    left: 0;
    padding: 0 120px;
    position: fixed;
    background: rgba(32,32,32, 0.5);
    width: 100%;
    font-size: 1.7rem;
    height: 56px;
}

.toolbar__navigation {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
}

.toolbar__logo {
    margin-left: 1rem;
}

.toolbar__logo a {
    color: #fff;
    text-decoration: none;
    font-size: 1.9rem;
}

.toolbar_navigation-items a {
    color: #fff;
    text-decoration: none;
}

.toolbar_navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: -webkit-flex;
    display: flex;
}

.spacer {
    -webkit-flex: 1 1;
            flex: 1 1;
}

.toolbar_navigation-items li {
    padding: 0 0.5rem;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
    color: darkturquoise !important;
}

@media screen and (max-width: 320px) {
    .toolbar {
        padding: 0px;
    }
}
.toggle-button {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 19px;
    width: 23px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button__line {
    width: 20px;
    background: #fff;
    height: 2px;
}

@media screen and (min-width: 321px) {
    .toggle-button {
        display: none;
    }
}

@media screen and (max-width: 320px) {
    .toggle-button {
        display: normal;
    }
}

.side-drawer {
    height: 100%;
    background: #fff;
    box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}

.side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

@-webkit-keyframes slide {
    from {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }

    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes slide {
    from {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }

    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.side-drawer li {
    margin: 0.5rem 0;
}

.side-drawer a {
    color: black;
    text-decoration: none;
    font-size: 1.2rem
}

.side-drawer a:hover,
.side-drawer a:active {
    color: rgba(0, 0, 0, 0.5)
}

.backdrop {
     position: fixed;
     width: 100%;
     height: 100%;
     top: 0;
     left: 0;
     background: rgba(0, 0, 0, 0.3);
     z-index: 100;
}
